<style lang="scss"  scoped>
  .home_right_box {
    width: 68%;
    height: auto;
    background: #fff;

    h2 {
      color: #333;
      font-size: 14px;
      line-height: 30px;
      background: #fff;
      font-weight: bold;
      border-bottom: 2px solid #555;
      padding: 12px 12px 6px 10px;
    }

    ul {
      padding: 20px;
      overflow: hidden;

      li {
        display: block;
      }

      //留言框
      .message_textarea {
        width: 100%;
        padding: 20px;
        background: #F7F7F7;
        position: relative;

        .textarea {
          width: 100%;
          height: 86px;
        }

        .text {
          display: inline-block;
          width: 200px;
          position: absolute;
          right: 20%;
        }
      }

      //提示消息
      .showinfo {
        padding: 8px;
        text-align: center;
        background-color: rgb(254, 242, 225);
        color: rgb(238, 84, 42);
        margin-top: 10px;
      }

      //总数
      .totalRegards {
        line-height: 54px;

        .all,
        .say {
          color: #FEB000;
        }
      }

      //留言展示
      .talk_box {
        height: 128px;
        box-sizing: border-box;
        padding: 30px 0 20px 0;
        clear: both;
        border-bottom: 1px solid #f1f1f1;

        .avator {
          width: 42px;
          height: 42px;
          margin-right: 12px;
          display: inline-block;

          img {
            width: 100%;
            border-radius: 50%;
          }
        }

        .text_box {
          width: calc(100% - 60px);
          height: 100%;
          display: inline-block;
          position: relative;

          span {
            display: block;
            width: 100%;
          }

          .top {
            margin-bottom: 10px;
            height: 21px;
            line-height: 21px;

            span {
              display: inline;
            }

            .title {
              font-weight: 700;
              font-size: 14px;
              color: #000;
              margin-right: 26px;
            }

            .time {
              font-weight: 400;
              font-size: 12px;
              color: #B2AAAA;
            }
          }

          .desc {
            height: 26px;
            line-height: 26px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
        }
      }
    }
  }
</style>


